/*** box sizing border-box for all elements ***/
*,
*::before,
*::after {
  box-sizing: border-box;
}
a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}
button {
  background-color: transparent;
  color: inherit;
  border-width: 0;
  padding: 0;
  cursor: pointer;
}
figure {
  margin: 0;
}
input::-moz-focus-inner {
  border: 0;
  padding: 0;
  margin: 0;
}
ul,
ol,
dd {
  margin: 0;
  padding: 0;
  list-style: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: inherit;
}
p {
  margin: 0;
}
cite {
  font-style: normal;
}
fieldset {
  border-width: 0;
  padding: 0;
  margin: 0;
}

// Fonts

@font-face {
  font-family: "Montserrat";
  src: url("fonts/Montserrat-Regular.eot");
  src: url("fonts/Montserrat-Regular.eot?#iefix") format("embedded-opentype"),
    url("fonts/Montserrat-Regular.woff") format("woff"),
    url("fonts/Montserrat-Regular.ttf") format("truetype"),
    url("fonts/Montserrat-Regular.svg#Montserrat-Regular") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  src: url("fonts/Montserrat-Light.eot");
  src: url("fonts/Montserrat-Light.eot?#iefix") format("embedded-opentype"),
    url("fonts/Montserrat-Light.woff") format("woff"),
    url("fonts/Montserrat-Light.ttf") format("truetype"),
    url("fonts/Montserrat-Light.svg#Montserrat-Light") format("svg");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  src: url("fonts/Montserrat-Bold.eot");
  src: url("fonts/Montserrat-Bold.eot?#iefix") format("embedded-opentype"),
    url("fonts/Montserrat-Bold.woff") format("woff"),
    url("fonts/Montserrat-Bold.ttf") format("truetype"),
    url("fonts/Montserrat-Bold.svg#Montserrat-Bold") format("svg");
  font-weight: bold;
  font-style: normal;
}

/* common functions scss */
$bgColor: #612d8c;
$whiteColor: #fff;
$selectBoxColor: #262229;
$primaryTextColor: #222728;
$lightTextColor: lighten($primaryTextColor, 26%);
$checkboxBg: rgba(87, 38, 128, 0.1);
$progressBorder: #7f44b1;
$infoBg: #ccc;
$cubic: cubic-bezier(0.64, 0.09, 0.08, 1);
$borderColor: rgba(34, 39, 40, 0.05);
$lightPrimary: lighten($primaryTextColor, 40%);
$lightBorder: lighten($primaryTextColor, 70%);

/*** Start with main page css ***/

* {
  margin: 0;
  padding: 0;
}
body {
  font-family: "Montserrat", sans-serif;
  background-color: $bgColor;
  padding: 120px 0 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $primaryTextColor;
  font-size: 1rem;
}
.container {
  max-width: 100%;
  width: 1140px;
  padding: 70px;
  background-color: $whiteColor;
  border-radius: 10px;
  margin: auto;
  box-shadow: 0px 13px 17.4px 2.6px rgba(36, 36, 36, 0.12);
}
.head {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 0 50px;
}
.language {
  align-self: flex-end;
}
.language-label {
  font-size: 0.7rem;
  font-weight: 300;
  padding: 0 10px 0 0;
}
.select-box {
  background-color: $whiteColor;
  color: $selectBoxColor;
  padding: 8px 20px 8px 10px;
  width: auto;
  border: none;
  font-size: 0.8rem;
  box-shadow: 0px 2px 9.7px 0.3px rgba(36, 36, 36, 0.08);
  -webkit-appearance: button;
  appearance: button;
  outline: none;
  cursor: pointer;
}
.title-h2 {
  font-size: 1.4rem;
  padding-bottom: 10px;
}
.para {
  font-weight: 300;
  color: $lightTextColor;
  line-height: 2rem;
}
.rights-ul {
  padding: 35px 0 20px 30px;
  li {
    padding: 0 0 20px;
  }
}
.custom-checkbox {
  position: relative;
  display: block;
  .info-icon {
    position: relative;
    cursor: pointer;
    background-color: $infoBg;
    border-radius: 50%;
    width: 15px;
    height: 15px;
    color: $whiteColor;
    display: inline-block;
    text-align: center;
    font-size: 0.5rem;
    font-weight: bold;
    padding: 4px 0;
    &:before {
      content: "";
      width: 0;
      height: 0;
      border-top: solid 7px transparent;
      border-bottom: solid 7px transparent;
      border-right: solid 7px #414142;
      opacity: 0;
      visibility: hidden;
      position: absolute;
      top: 30%;
      left: 19px;
      transition: opacity 0.2s $cubic, transform 0.2s $cubic;
      z-index: 3;
      transform: translateX(0) translateY(-20%);
    }
    &:hover {
      &:before {
        display: block;
        opacity: 1;
        visibility: visible;
        transform: translateX(0) translateY(-20%);
      }
    }
    &:after {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      content: attr(data-tooltip);
      padding: 6px 10px;
      background: #414142;
      color: $whiteColor;
      white-space: nowrap;
      z-index: 2;
      border-radius: 3px;
      transition: opacity 0.2s $cubic, transform 0.2s $cubic;
      top: -4px;
      left: 100%;
      transform: translateX(10%) translateY(0);
      font-size: 0.8rem;
      border: none;
      outline: none;
    }
    &:hover {
      &:after {
        display: block;
        opacity: 1;
        visibility: visible;
        transform: translateX(12%) translateY(0);
      }
    }
  }
}
input[type="checkbox"].css-checkbox {
  position: absolute;
  z-index: -1000;
  left: -1000px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}

input[type="checkbox"].css-checkbox + label.css-label {
  padding-left: 25px;
  display: inline-block;
  background-repeat: no-repeat;
  background-position: 0 0;
  font-size: 0.9rem;
  vertical-align: middle;
  cursor: pointer;
}
label.css-label {
  position: relative;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  &::before {
    position: absolute;
    content: "";
    width: 16px;
    height: 16px;
    background-color: $checkboxBg;
    left: 0;
    top: 0px;
    border-radius: 2px;
  }
}
input[type="checkbox"].css-checkbox:checked + label.css-label::before {
  background-color: $bgColor;
}
input[type="checkbox"].css-checkbox:checked + label.css-label:after {
  left: 5px;
  top: 2px;
  width: 6px;
  height: 9px;
  border: solid white;
  border-width: 0 2px 2px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  position: absolute;
  content: "";
}
[class*="input-button"] {
  border: 1px solid $bgColor;
  background-color: $bgColor;
  color: $whiteColor;
  text-transform: uppercase;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 3px;
  padding: 10px 30px;
  cursor: pointer;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  &:hover {
    background-color: #7e48aa;
  }
}
.input-button-back {
  border-color: $borderColor;
  background-color: $whiteColor;
  color: lighten($primaryTextColor, 40%);
  &:hover {
    color: $whiteColor;
  }
}

/*** Progress Bar ***/

.progress-bar {
  display: flex;
  justify-content: space-between;
  list-style: none;
  padding: 0;
  margin: 0 0 1rem 0;
  max-width: 100%;
  width: 760px;
  margin: 40px auto;

  li {
    flex: 2;
    position: relative;
    padding: 14px 0 0 0;
    font-size: 0.9rem;
    line-height: 1.5;
    color: $whiteColor;
    white-space: nowrap;
    overflow: visible;
    min-width: 0;
    text-align: center;
    border-top: 2px solid $progressBorder;

    &:first-child {
      flex: 1;
    }

    &:last-child {
      flex: 1;
      text-align: right;
    }

    &:before {
      content: "";
      display: block;
      width: 8px;
      height: 8px;
      background-color: $progressBorder;
      border-radius: 50%;
      position: absolute;
      left: calc(50% - 6px);
      top: -5px;
      z-index: 3;
      transition: all 0.2s ease-in-out;
    }

    &:first-child:before {
      left: 0;
    }

    &:last-child:before {
      right: 0;
      left: auto;
    }
  }

  span {
    transition: opacity 0.3s ease-in-out;
  }

  li:not(.is-active) span {
    opacity: 0.34;
  }

  .is-complete:not(:first-child):after,
  .is-active:not(:first-child):after {
    content: "";
    display: block;
    width: 100%;
    position: absolute;
    top: -2px;
    left: -50%;
    z-index: 2;
    border-top: 2px solid $whiteColor;
  }
  li:first-child span {
    display: inline-block;
    position: absolute;
    left: -25%;
  }
  li:last-child span {
    display: inline-block;
    position: absolute;
    left: 60%;
  }

  .is-complete:last-child:after,
  .is-active:last-child:after {
    width: 200%;
    left: -100%;
  }

  .is-complete:before {
    background-color: $whiteColor;
  }

  .is-active:before,
  li:hover:before,
  .is-hovered:before {
    background-color: $whiteColor;
  }

  li:hover:before,
  .is-hovered:before {
    transform: scale(1.33);
  }

  li {
    &:hover span,
    &.is-hovered span,
    &.is-complete span {
      opacity: 1;
    }
  }
}

/*** Other Pages ***/
.min-height-div {
  min-height: 500px;
  padding: 20px 0 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  .width-50:nth-child(2) {
    padding: 0 40px;
    align-self: center;
    text-align: center;
  }
}
.width-50 {
  max-width: 100%;
  width: 50%;
  padding: 0 40px 0 0;
}
.form-control {
  padding: 0 0 30px;
  display: flex;
  flex-direction: column;
}
[class*="form-label"] {
  font-size: 0.8rem;
  padding-bottom: 10px;
  color: $lightTextColor;
}
.form-label-p {
  color: $lightPrimary;
}
.form-select-wrap {
  border: 1px solid $borderColor;
  border-radius: 5px;
  position: relative;
  &::after {
    position: absolute;
    content: " ";
    width: 12px;
    height: 12px;
    top: 15px;
    right: 15px;
    transform: rotate(45deg);
    z-index: 2;
    border-right: 2px solid $lightBorder;
    border-bottom: 2px solid $lightBorder;
  }
  select {
    appearance: none;
    width: 100%;
    border: none;
    padding: 15px;
    color: $lightPrimary;
    font-size: 0.9rem;
    border-radius: 5px;
    cursor: pointer;
  }
}
.form-input {
  color: $lightPrimary;
  font-size: 1.2rem;
  border-radius: 5px;
  border: 1px solid $borderColor;
  padding: 15px;
  width: 100%;
}
.iti__flag-container {
  border-right: 1px solid $borderColor;
  z-index: 1;
}
.otp-wrap {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  .form-input {
    width: 60px;
    text-align: center;
  }
}
.input-button-otp {
  background-color: $whiteColor;
  color: $bgColor;
  text-transform: capitalize;
  width: 20%;
  padding: 7px 15px;
  margin: 15px 0 0;
  font-weight: normal;
  font-size: 0.9rem;
  &:hover,
  &:focus,
  &:active {
    color: $whiteColor;
  }
}
.upload-image-instructions {
  text-align: center;
  line-height: 24px;
  color: $lightPrimary;
  padding: 0 0 40px;
}

/*** File Upload ***/
.file-upload-btn {
  border-radius: 5px;
  padding: 30px;
  border: 1px solid $borderColor;
  display: flex;
  align-items: center;
  justify-content: center;
  color: $lightTextColor;
  width: 100%;
  i {
    margin-right: 10px;
  }
}
.supported-types {
  display: block;
  text-align: right;
  font-size: 0.8rem;
  color: $lightPrimary;
  padding: 5px 0 20px;
}
.file-upload-input {
  display: none;
}
.file-upload-content {
  display: flex;
  flex-direction: row;
  margin: 20px 0;
  align-items: center;
}
.upload-img {
  border-radius: 5px;
  background-color: #f7f3f2;
  padding: 10px;
  margin: 0 20px 0 0;
  min-height: 164px;
  display: flex;
  align-items: center;
  img {
    max-height: 144px;
    max-width: 144px;
  }
}
.image-title-wrap {
  display: flex;
  flex-direction: column;
}
.image-title {
  color: $lightPrimary;
  font-size: 0.9rem;
  padding: 0 0 15px;
}
.remove-image {
  background-color: $bgColor;
  border-radius: 50%;
  width: 35px;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/*** Sprite Image ***/
.sprite {
  background-image: url(../img/sprites.png);
  background-repeat: no-repeat;
  display: inline-block;
}

.sprite-agent-icon {
  width: 24px;
  height: 31px;
  background-position: -2px -2px;
}

.sprite-check-audio {
  width: 12px;
  height: 15px;
  background-position: -30px -2px;
}

.sprite-check-mic {
  width: 10px;
  height: 15px;
  background-position: -46px -2px;
}

.sprite-check-video {
  width: 14px;
  height: 16px;
  background-position: -60px -2px;
}

.sprite-remove {
  width: 18px;
  height: 18px;
  background-position: -30px -22px;
}

.sprite-upload-icon {
  width: 24px;
  height: 20px;
  background-position: -52px -22px;
}

.sprite-video-icon {
  width: 61px;
  height: 61px;
  background-position: -80px -2px;
}

/*** Bottom Navigation Buttons ***/

.bottom-nav {
  display: flex;
  justify-content: space-between;
}
