.custom-checkbox {
  position: relative;
  display: block; }
  .custom-checkbox .info-icon {
    position: relative;
    cursor: pointer;
    background-color: #ccc;
    border-radius: 50%;
    width: 15px;
    height: 15px;
    color: #fff;
    display: inline-block;
    text-align: center;
    font-size: 0.5rem;
    font-weight: bold;
    padding: 4px 0; }
    .custom-checkbox .info-icon:before {
      content: "";
      width: 0;
      height: 0;
      border-top: solid 7px transparent;
      border-bottom: solid 7px transparent;
      border-right: solid 7px #414142;
      opacity: 0;
      visibility: hidden;
      position: absolute;
      top: 30%;
      left: 19px;
      transition: opacity 0.2s cubic-bezier(0.64, 0.09, 0.08, 1), transform 0.2s cubic-bezier(0.64, 0.09, 0.08, 1);
      z-index: 3;
      transform: translateX(0) translateY(-20%); }
    .custom-checkbox .info-icon:hover:before {
      display: block;
      opacity: 1;
      visibility: visible;
      transform: translateX(0) translateY(-20%); }
    .custom-checkbox .info-icon:after {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      content: attr(data-tooltip);
      padding: 6px 10px;
      background: #414142;
      color: #fff;
      white-space: nowrap;
      z-index: 2;
      border-radius: 3px;
      transition: opacity 0.2s cubic-bezier(0.64, 0.09, 0.08, 1), transform 0.2s cubic-bezier(0.64, 0.09, 0.08, 1);
      top: -4px;
      left: 100%;
      transform: translateX(10%) translateY(0);
      font-size: 0.8rem;
      border: none;
      outline: none; }
    .custom-checkbox .info-icon:hover:after {
      display: block;
      opacity: 1;
      visibility: visible;
      transform: translateX(12%) translateY(0); }

input[type="checkbox"].css-checkbox {
  position: absolute;
  z-index: -1000;
  left: -1000px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0; }

input[type="checkbox"].css-checkbox + label.css-label {
  padding-left: 25px;
  display: inline-block;
  background-repeat: no-repeat;
  background-position: 0 0;
  font-size: 0.9rem;
  vertical-align: middle;
  cursor: pointer; }

label.css-label {
  position: relative;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }
  label.css-label::before {
    position: absolute;
    content: "";
    width: 16px;
    height: 16px;
    background-color: rgba(87, 38, 128, 0.1);
    left: 0;
    top: 0px;
    border-radius: 2px; }

input[type="checkbox"].css-checkbox:checked + label.css-label::before {
  background-color: #612d8c; }

input[type="checkbox"].css-checkbox:checked + label.css-label:after {
  left: 5px;
  top: 2px;
  width: 6px;
  height: 9px;
  border: solid white;
  border-width: 0 2px 2px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  position: absolute;
  content: ""; }

